.modal {
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
  }
.closebtn{
    padding-top: 20px;
    background-color:"white";
    color:'#006666';
    margin:'auto';
    align-items: center;
    display: flex;
    justify-content: center;
}